    arrClassScc: ["", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", ""],
    kkk: 1
  }),
  metaInfo: {
    title: 'ý nghĩa truyện cổ tích cây khế',
    meta: [
      { name: 'thumbnail', content: 'https://cotich.net/uploads/news/a_807286440_ynghiatruyencotichcaykhe.jpeg' },
      { charset: 'utf-8' },
      { name: 'Keywords', content: 'ý nghĩa truyện cổ tích cây khế' },
